<template>
  <div id="app">
    <h2
      v-if="env=='production'"
      style="
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 4vw;
        color: #64b570;
        z-index: 22;
        background: #fffffff7;
        padding: 45px;
        border-radius: 15px;
        box-shadow: 0 0 25px 0px #8bc894;
        box-sizing: border-box;
        margin: 0;
      "
    >Under Development</h2>
    <Layout
      :layout="layout"
    >
      <router-view></router-view>
    </Layout>
    <!-- alert component -->
    <Alert
      v-if="alertData"
      :data="alertData"
      @close="$store.dispatch('init/alert')"
    />
    <!-- <div
      id="cursor"
      v-if="cursor.show"
      :style="`
        left: ${cursor.x}px;
        top: ${cursor.y}px;
      `"
    ></div> -->
  </div>
</template>
<script>
// @ is an alias to /src
import Alert from '@c/alert'
import Layout from '@l/Layout'
import { mapGetters } from 'vuex'

export default {
  name: 'App',
  data(){
    return {
      cursor:{
        x: 0,
        y: 0,
        show: true
      }
    }
  },
  computed:{
    ...mapGetters({
      alertData:'init/alert'
    }),
    layout(){
      const r = this.$route;
      return r && r.meta ? r.meta.layout || null : null;
    },
    env(){
      return process.env.NODE_ENV
    }
  },
  methods:{
    manageListeners(remove){
      const call = remove ? 'removeEventListener' : 'addEventListener';
      [ 'moveCursor', 'hideCursor', 'showCursor' ]
      .forEach(m=> document.body[call]('mousemove',this[m]))
    },
    moveCursor(e){
      this.cursor.x = e.x
      this.cursor.y = e.y
    },
    hideCursor(){
      this.cursor.show = false
    },
    showCursor(e){
      this.cursor.x = e.x
      this.cursor.y = e.y
      this.cursor.show = true
    },
  },
  mounted(){
    this.manageListeners()
  },
  beforeDestroy(){
    this.manageListeners(true)
  },
  components:{
    Alert,
    Layout
  }
}
</script>
<style
  lang="scss"
  src="~/app.scss"
></style>