<template>
  <header class="layout-px">
    <logo />
    <button
      class="menu-toggler"
      :class="{open:toggle}"
      type="button"
      @click="toggleMenu"
    ></button>
  </header>
</template>

<script>
  import logo from '@c/logo'
  export default {
    name: 'layout-header',
    data(){
      return {
        toggle: false
      }
    },
    components:{
      logo
    },
    methods:{
      toggleMenu(){
        this.toggle = !this.toggle;
      }
    }
  }
</script>

